'use client';

import Link from "next/link";
import { usePathname } from "next/navigation";

export default function Sidebar() {
    const pathname = usePathname();

    const tabs = [
        { key: 'user', label: 'Profilim' },
        { key: 'referrals', label: 'Referanslar' },
        { key: 'apikeys', label: 'API Anahtarları' },
        { key: 'history', label: 'Giriş Geçmişi' },
        { key: '2fa', label: '2FA' },
        { key: 'password', label: 'Parola Değiştir' },
    ];

    return (
        <div className="sidebar">
            {tabs.map((tab) => (
                <Link key={tab.key} href={`/profile/${tab.key}`}>
                    <div className={`tab ${pathname.includes(tab.key) ? 'active' : ''}`}>
                        {tab.label}
                    </div>
                </Link>
            ))}
        </div>
    );
}
